  flex-direction: column; }

.close {
  opacity: 0.3;
  margin: 32px; }

.close:hover {
  opacity: 1; }

.close:before, .close:after {
  position: absolute;
  content: '';
  height: 33px;
  width: 2px;
  background-color: white; }

.close:before {
  transform: rotate(45deg); }

.close:after {
  transform: rotate(-45deg); }

#topic-container {
